import { makeStyles, useTheme } from '@material-ui/styles';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  content: {
    backgroundColor: theme.palette.mainBackground,
    width: '576px',
    height: '100%',
    boxShadow: '0px 0px 20px grey',
  },
  public: {
    '& .ql-align-right': {
      textAlign: 'right'
    },
    '& .ql-align-left': {
      textAlign: 'left'
    },
    '& .ql-align-center': {
      textAlign: 'center'
    },
    color: theme.palette.gray,
    fontSize: '0.9375em',
 
    fontWeight: 400,
  },
  normalBlock: {
    padding: '30px 30px 30px 30px',
    // height: '100%',
    '& img': {
      width: '100%'
    },
    border: theme.palette.contrastColor
  },
  controlBlock: {
    padding: '30px 30px 70px 30px',
    height: '100%',
    textAlign: 'center',
    border: theme.palette.contrastColor
  },
  buttonBlock: {
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('ms')]: {
      display: 'flex',
      justifyContent: 'center',
    },
  },
  description: {
    fontSize: 16,
    fontWeight: 400,
    lineHeight: '24px',
    color: theme.palette.text_sub
  },
  header: {
    color: theme.palette.gray,
    fontSize: '1.375em',
 
    fontWeight: 400
  },
  subHeader: {
    color: theme.palette.gray,
    fontSize: '0.9375em',
 
    fontWeight: 400,
    marginTop: '60px',
    marginBottom: '30px'
  },
  nftHeader: {
    display: "flex",
    alignItems: 'center',
    justifyContent: 'space-between',
    margin: "2rem 0 0 0",
    '& button': {
      textTransform: "capitalize"
    },
    [theme.breakpoints.down('xs')]: {
      '& $lowestPrice': {
        width: 'min-content !important',
      }
    },

  },
  lowestPrice: {
    textAlign: 'center',
  },
  priceFilteringBtn: {
    width: "140px",
    height: "29px",
    background: "#E0E3FF",
    borderRadius: "4px",
    padding: "6px 16px",
    [theme.breakpoints.down('sm')]: {
      display: "column",
      alignItems: 'center',
      
      
    },
  },
  title20: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "20px",
    lineHeight: "28px",
    color: "#3F4271"
  },
  textItem14: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "14px",
    lineHeight: "17px",
    display: "flex",
    alignItems: "center",
    color: "#3F4271"
  },
  subtitle16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "16px",
    lineHeight: "24px",
    color: "#797B9C"
  },
  btnOpen: {
    '& .MuiButton-label': {
        textTransform: 'none',
        fontSize: '1.1em',
    },
    '& .MuiButton-containedSecondary:hover': {

    },
    '&:hover': {
        backgroundColor: theme.palette.pink
    },
    padding: '4px',
    border: 'none',
    fontWeight: '300',
    backgroundColor: theme.palette.pink,
    color: theme.palette.black_white,
    marginLeft: '20px',
    marginRight: '20px',
    marginBottom: '20px',
    paddingLeft: '40px',
    paddingRight: '40px',
    width: '400px',
    
  },
  progressContainer: {
    position: 'absolute',
    top: '50%',
    width: 'calc(100% - 50px)',
    display: 'flex',
    justifyContent: 'center',
  },
  progress: {
      color: theme.palette.pink
  },
  tooltip: {
    fontSize: '0.8em'
  },
  cardContainer: {
    '& div:last-child': {
      borderBottom: "none",
      paddingBottom: "0"
    }
  },
  loading: {
    display: 'flex', 
    justifyContent: 'center', 
    marginTop: 20,
    width: '100% !important',
    color: theme.palette.blue
  }
}));

export default useStyles;
