import React, { useCallback, useEffect, useState } from 'react';
import { useHistory } from "react-router-dom";
import Web3 from 'web3';
import { makeStyles, useTheme } from '@material-ui/styles';
import {
  Box,
  Grid,
  Button,
  Typography,
  Link,
} from '@material-ui/core';
import share from "../icons/share.svg";
import { useSelector } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start",
    padding: "2rem 0",
    borderBottom: "1px solid rgba(178, 183, 255, 0.2)",
    '& button': {
      textTransform: "capitalize"
    },
  },
  nftImg: {
    width: '90%',
    cursor: "pointer",
    borderRadius: "12px",
    [theme.breakpoints.down('xs')]: {
      width: '100%',
    },
  },
  title24: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "24px",
    lineHeight: "36px",
    color: "#3F4271",
    cursor: "pointer",
  },
  cardTitleGroup: {
    '& img': {
      width: "2rem",
      height: "2rem",
      borderRadius: "50%",
      marginRight: "1rem",
      cursor: "pointer",

    },

    [theme.breakpoints.down('xs')]: {
      marginTop: "1rem",
    },

  },
  subtitle16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "16px",
    lineHeight: "24px",
    color: "#797B9C"
  },
  buttonText16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "16px",
    lineHeight: "19px",
    color: "#3F4271"
  },
  regularText14: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "14px",
    lineHeight: "20px",
    color: "#3F4271",
    textTransform: "none",
  },
  text16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "16px",
    lineHeight: "19px",
    color: "#8087ED"
  },
  itemBtn: {
    background: "rgba(178, 183, 255, 0.2)",
    borderRadius: "4px",
  },
  ellipse2: {
    width: "6px",
    height: "6px",
    background: "#8087ED",
    borderRadius: "50%",
    margin: "0 1rem"
  },
  marketPlace: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start",
    marginTop: "1rem",
    '& div:first-child': {
      marginRight: "2rem"
    }
  }
}));

function Card({ asset, nft }) {
  const classes = useStyles();
  const history = useHistory();
  const [price, setPrice] = useState();


  const url = `/nfts/${asset.asset_contract.address}/${asset.token_id}`;

  useEffect(() => {
    const nftListObj = {}
    nftListObj[asset.token_id] = nft;
  },[asset])
  
  
  const nextInfo = useCallback(() => {
    history.push(url)
  }, [history, url])


  useEffect(() => {
    const displayingPrice = () => {
      if(!nft.last_sale) return
      if (nft.last_sale.event_type === 'created'){
        setPrice(nft.last_sale.starting_price)
      } else if (nft.last_sale.event_type === 'successful') {
        setPrice(nft.last_sale.total_price)
      } else if (nft.last_sale.event_type === 'cancelled') {
        setPrice(nft.last_sale.total_price ?? nft.last_sale.ending_price)
      } else if (nft.last_sale.event_type === 'bid_entered' ) {
        setPrice(nft.last_sale.bid_amount)
      } else if (nft.last_sale.event_type === 'bid_withdrawn') {
        setPrice(nft.last_sale.total_price ?? nft.last_sale.starting_price)
      } else if (nft.last_sale.event_type === 'transfer') {
        setPrice("")
      } else if (nft.last_sale.event_type === 'offer_entered') {
        setPrice(nft.last_sale.bid_amount)
      } else if (nft.last_sale.event_type === 'approve') {
        setPrice("")
      }
    }
    displayingPrice()
  },[])

  return (
    <Grid container
      className={classes.root}
    >
      <Grid item xs={12} sm={6} md={4} lg={3}>
        <Box
          component="img"
          alt=""
          src={asset?.image_url ?? ''}
          className={classes.nftImg}
          onClick={nextInfo}
        />
      </Grid>
      <Grid item xs={12} sm={6} md={8} lg={8}>
        <Box>
          <Box
            className={classes.cardTitleGroup}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: '',
              marginBottom: "1rem"
            }}
            onClick={nextInfo}
          >
            <Box
              component="img"
              alt=""
              src={asset?.collection?.image_url ?? ''}
              style={{ marginRight: ".5rem" }}
            />
            <Typography
              className={classes.title24}
            > 
              {asset.collection.name ?? 'name...'}
            </Typography>
          </Box>
          <Box
            style={{ display: "flex", alignItems: "center", justifyContent: '', margin: "0 0 1rem 0" }}
          >
            <Button className={classes.itemBtn}>
              <span className={classes.regularText14}>
                You have: 1 item
              </span>
            </Button>
            {
              price ? 
                <>
                  <Box className={classes.ellipse2}></Box>
                  <Typography className={classes.buttonText16}>
                    { Web3.utils.fromWei(price)} ETH
                  </Typography>
                </>
              : ""
            }
          </Box>
          <Typography className={classes.subtitle16}> {asset.collection.description ?? ''} </Typography>

          <Box className={classes.marketPlace}>
            <Link className={classes.text16} href={asset.permalink}>
              Opensea <Box component="img" alt="share" src={share} style={{ marginLeft: "5px", marginRight: "1.2rem"}} />
            </Link>
            <Link className={classes.text16} href={`https://rarible.com/token/${asset.asset_contract.address}:${asset.token_id}`}>
              Rarible <Box component="img" alt="share" src={share} style={{ marginLeft: "5px" }} />
            </Link>
          </Box>
        </Box>
      </Grid>
    </Grid>
  )
}

export default Card;