import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import useStyles from './style';
import {
  Box,
  Button,
  Typography,
  CircularProgress
} from '@material-ui/core';
import { Card } from './components';
import { getNFTsAssets } from '../../service/nft'

const Nfts = props => {
  const account = useSelector(state => state.app.currentWallet);
  
  const classes = useStyles()
  const [nftsAssets, setNftsAssets] = useState([]);
  const [loading, setLoading] = useState(true);

  // Retrieve an asset  0x94d66fAc041FD042cFD0218df1c3FeddE8d11f52

  const fetchNFTs = useCallback(async () => {
    getNFTsAssets(`https://api.opensea.io/api/v1/assets?owner=${account}&order_direction=asc&include_orders=true`, async(err, data)=> {
      if (err) {
        setLoading(false)
      }
      const assets = await data.assets;
      setNftsAssets(assets)
      setLoading(false)
    })
  },[account])

  
  useEffect(() => {
    if (account) {
      fetchNFTs();
    }
  }, [account])
  
  const [sortedAssets, setSortedAssets] = useState(nftsAssets)

  const handlePrice = useCallback(() => {
    let price;
    const addingPriceProp = nftsAssets.map(nft => {
      if (nft?.last_sale) {
        if (nft.last_sale.event_type === 'created') {
          price = nft.last_sale.starting_price
        } else if (nft.last_sale.event_type === 'successful') {
          price = nft.last_sale.total_price
        } else if (nft.last_sale.event_type === 'cancelled') {
          price = nft.last_sale.total_price ?? nft.last_sale.ending_price
        } else if (nft.last_sale.event_type === 'bid_entered') {
          price = nft.last_sale.bid_amount
        } else if (nft.last_sale.event_type === 'bid_withdrawn') {
          price = nft.last_sale.total_price ?? nft.last_sale.starting_price
        } else if (nft.last_sale.event_type === 'transfer') {
          price = 0
        } else if (nft.last_sale.event_type === 'offer_entered') {
          price = nft.last_sale.bid_amount
        } else if (nft.last_sale.event_type === 'approve') {
          price = 0
        }
        nft["price"] = price;
        return nft;
      } else {
        price = 0;
        nft["price"] = price;
        return nft;
      }

    }).sort((a, b) => {
      return a.price - b.price
    })
    setSortedAssets(addingPriceProp)

  }, [nftsAssets])

  useEffect(() => {
    handlePrice()
  },[nftsAssets])


  // Lowest Price & Last Price
  const [isLowestPrice, setIsLowestPrice] = useState(true);
  const [isLastPrice, setIsLastPrice] = useState(false);

  const handleToggle = (type) => {
    setIsLowestPrice(!isLowestPrice);
    setIsLastPrice(!isLastPrice);
    
    if(type === 'asc') {
      handlePrice()
      const ascendingSorted = nftsAssets.sort((a,b) => {
        return a.price - b.price
      })
      setSortedAssets(ascendingSorted);
    } else if(type === 'desc') {
      handlePrice()
      
    }
  }
    
  return (
    <Box>
      <Typography className={classes.description}>
        {
          sortedAssets.length > 0 && sortedAssets[0].name ?
            "List of NFT pictures that are in your wallet"
          : "No NFT in your wallet"
        }
      </Typography>
      { sortedAssets.length > 0 && sortedAssets[0].name ? 
        <Box>
          <Box className={classes.nftHeader}>
            <Box style={{background: "rgba(178, 183, 255, 0.2)", padding: "5px"}} className={classes.lowestPrice}>
              <Button
                className={isLowestPrice ? classes.priceFilteringBtn : ""}
                onClick={() => handleToggle('asc')}
              >
                <span className={classes.textItem14}> Lowest price </span>
              </Button>
              <Button
                className={isLastPrice ? classes.priceFilteringBtn : ""}
                onClick={() => handleToggle('desc')}
              >
                <span className={classes.textItem14}> Last price </span>
              </Button>
            </Box>
            <Typography> 
              <span className={classes.subtitle16}>
                Total balance:
              </span> 
              <span className={classes.title20}>$7,018.65</span> 
            </Typography>
          </Box>

          <Box className={classes.cardContainer}>
            {
              sortedAssets.map((nft) => {
                return <Card key={nft.id} asset={nft} nft={nft}/>
              })
            }
          </Box>
        </Box>
        :
        loading && <CircularProgress className={classes.loading}/>
      }
    </Box>
  );
};

export default Nfts;
